import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import * as moment from "moment";
import { environment } from "../../environments/environment";
import { ModalController } from '@ionic/angular';
import { ModalPage } from '../modal/modal.page';

@Component({
  selector: 'app-telefones',
  templateUrl: './telefones.page.html',
  styleUrls: ['./telefones.page.scss'],
})
export class TelefonesPage implements OnInit {
  private endereco: string;
  private total: number;
  private telefones: any[];
  private API_URL = environment.API_URL;

  constructor(
    private route: ActivatedRoute, 
    private http: HttpClient,
    private modalController: ModalController,
  ) { }

  ngOnInit() {
    moment.locale('pt-br');
    this.endereco =  this.route.snapshot.paramMap.get('endereco');
    this.http.get(`${this.API_URL}/telefones/${this.endereco}`).subscribe(res => {
      this.total = res[0].total;
      this.telefones = res[0].telefones;

      this.endereco = `${this.endereco} - Total: ${this.total}`;

      this.telefones = this.telefones.map(telefone => {
        telefone.ultimaVezConsultado = telefone.ultimaVezConsultado != '' ? moment(telefone.ultimaVezConsultado).fromNow() : 'Nunca';
        return telefone;
      })
    });
  }

  go(item) {
    window.open(`tel:${item.telefone}`, '_system');
  }

  async presentModal(telefone) {
    const modal = await this.modalController.create({
      component: ModalPage,
      componentProps: {
        'telefone': telefone
      }
    });
    return await modal.present();
  }
}
